import React from 'react'
import sss from '../../assets/images/SSS-4.png'
import { Link } from 'react-router-dom'

export default function Navbar() {
    return (
        <div>
            <nav className="navbar navbar-expand-lg navbar-light bg-light">
  <Link className="navbar-brand" to="/"><img alt="error" src={sss} width={50} height={50}/></Link>
  <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
    <span className="navbar-toggler-icon"></span>
  </button>

  <div className="collapse navbar-collapse" id="navbarSupportedContent">
    <ul className="navbar-nav mr-auto">
      <li className="nav-item dropdown">
        <a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
          Sports
        </a>
        <div className="dropdown-menu" aria-labelledby="navbarDropdown">
          <Link className="dropdown-item" to="/nba">NBA 2K</Link>
          <Link className="dropdown-item" to="/fifa">FIFA</Link>
          <Link className="dropdown-item" to="/nfl">Madden</Link>
          <Link className="dropdown-item" to="/wwe">WWE 2K</Link>
          <Link className="dropdown-item" to="/nhl">NHL</Link>
          <Link className="dropdown-item" to="/mlb">MLB The Show</Link>
          <Link className="dropdown-item" to="/pes">PES</Link>
        </div>
      </li>
      <li className="nav-item dropdown">
        <a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
          Events
        </a>
        <div className="dropdown-menu" aria-labelledby="navbarDropdown">
          <Link className="dropdown-item" to="/events">Enter May Events</Link>
          <Link className="dropdown-item" to="/nba">Schedules</Link>
        </div>
      </li>

      <li className="nav-item dropdown">
        <a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
          FAQ
        </a>
        <div className="dropdown-menu" aria-labelledby="navbarDropdown">
        <Link className="nav-link" to="/settings">Game Settings</Link>
        <Link className="nav-link" to="/rules">Game Rules</Link>
        <Link className="nav-link" to="/about">About Us</Link>
        </div>
      </li>
    </ul>
    <div className="form-inline my-2 my-lg-0">
      <Link className="btn btn-danger my-2 my-sm-0" to="/signup">Sign Up</Link>
    </div>
  </div>
</nav>
        </div>
    )
}
