import React, { Component } from 'react'

export default class Form extends Component {
  state={
   choice1: ["Randy Orton","Rusev","Drake Maverick","Andrade","The Rock"],
   choice2: ["Randy Orton","Rusev","Drake Maverick","Andrade","The Rock"],
   choice3: ["Randy Orton","Rusev","Drake Maverick","Andrade","The Rock"],
   choice4: ["Randy Orton","Rusev","Drake Maverick","Andrade","The Rock"] ,
   choice5: ["Randy Orton","Rusev","Drake Maverick","Andrade","The Rock"] ,
   choice6: ["Randy Orton","Rusev","Drake Maverick","Andrade","The Rock"] 
  }

  
 shuffle=()=> {
   this.setState({choice1:this.state.choice1.sort(() => Math.random() - 0.5),
    choice1:this.state.choice2.sort(() => Math.random() - 0.5),
    choice1:this.state.choice3.sort(() => Math.random() - 0.5),
    choice1:this.state.choice4.sort(() => Math.random() - 0.5),
    choice1:this.state.choice5.sort(() => Math.random() - 0.5),
    choice1:this.state.choice6.sort(() => Math.random() - 0.5),
  })
 }

  render() {
    return (
        <div className="container" style={{marginBottom:"5%"}}>
            <div style={{padding:"40px"}}>
            <form action="/success">
  <div class="form-group">
    <label for="exampleInputEmail1">Email address</label>
    <input type="email" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter email"/>
    <small id="emailHelp" class="form-text text-muted">We'll never share your email with anyone else.</small>
  </div>
  <div class="form-group">
    <label for="exampleInputPassword1">Password</label>
    <input type="password" class="form-control" id="exampleInputPassword1" placeholder="Password"/>
  </div>
  <div class="form-group" style={{textAlign:"center"}}> 
    <input type="button" className="btn btn-danger" style={{width:"100%"}} onClick={this.shuffle} value="Randomize Picks"/>
  </div>  
  <div class="form-group">
    <label>NBA Cinco de Mayo Celebración Sim Cup</label>
    <select className="form-control">
    {this.state.choice1.map( e =>(<option>{e}</option>
      ))}
    </select>
  </div>
  
  <div class="form-group">
    <label htmlFor="exampleFormControlSelect1">NBA Cinco de Mayo Celebración Sim Cup</label>
    <select class="form-control" id="exampleFormControlSelect1">
    {this.state.choice2.map((e) =>{
        return(
      <option>{e}</option>
      )})}
    </select>
  </div>

  <div class="form-group">
    <label htmlFor="exampleFormControlSelect1">NBA Cinco de Mayo Celebración Sim Cup</label>
    <select class="form-control" id="exampleFormControlSelect1">
    {this.state.choice3.map((e) =>{
        return(
      <option>{e}</option>
      )})}
    </select>
  </div>

  <div class="form-group">
    <label htmlFor="exampleFormControlSelect1">NBA Cinco de Mayo Celebración Sim Cup</label>
    <select class="form-control" id="exampleFormControlSelect1">
    {this.state.choice4.map((e) =>{
        return(
      <option>{e}</option>
      )})}
    </select>
  </div>
  <div class="form-group">
    <label htmlFor="exampleFormControlSelect1">NBA Cinco de Mayo Celebración Sim Cup</label>
    <select class="form-control" id="exampleFormControlSelect1">
    {this.state.choice5.map((e) =>{
        return(
      <option>{e}</option>
      )})}
    </select>
  </div>
  <div class="form-group">
    <label htmlFor="exampleFormControlSelect1">NBA Cinco de Mayo Celebración Sim Cup</label>
    <select class="form-control" id="exampleFormControlSelect1">
    {this.state.choice6.map((e) =>{
        return(
      <option>{e}</option>
      )})}
    </select>
  </div>  
  <button type="submit" class="btn btn-primary" style={{width:"100%"}}>Submit</button>
</form>

</div>        
      </div>
    )
  }
}
