import React from 'react'

export default function History() {
    return (
        <div>
<div class="jumbotron history jumbotron-fluid">
<div className="overlay"></div>
                <div className="inner">
  <div class="container text-center">
    <h1 class="display-4 text-light">Event History</h1>
  </div>
  </div>
</div>
<div className="text-center" style={{padding:"20px"}}>
<h1>Check out the past winners for each event</h1>
</div>
<div className="container row text-center" style={{margin:"0 auto", lineHeight:"3", padding:"20px"}}> 
<h2 style={{margin:"0 auto"}}>No Current History</h2>
</div>
        </div>
    )
}
