import React from 'react';
import './App.css';
import Rules from './components/pages/Rules'
import NavBar from './components/Base/Navbar'
import Home from './components/pages/Home'
import Footer from './components/Base/Footer'
import About from './components/pages/about/About'
import History from './components/pages/History'
import Settings from './components/pages/Settings'
import Membership from './components/pages/Membership'
import NBA from './components/teams/NBA'
import FIFA from './components/teams/FIFA'
import Madden from './components/teams/Madden'
import MLB from './components/teams/MLB'
import PES from './components/teams/PES'
import WWE2K from './components/teams/WWE2K'
import NHL from './components/teams/NHL'
import Events from './components/pages/Form'
import Success from './components/pages/about/Success'

import { BrowserRouter as Router, Route, Switch } from 'react-router-dom'

function App() {
  return (
    <Router>
    <div className="App">
    <NavBar/>
    <Switch>
    <Route exact path="/" component={Home}/>
    <Route exact path="/about" component={About}/>
    <Route exact path="/rules" component={Rules}/>
    <Route exact path="/settings" component={Settings}/>
    <Route exact path="/history" component={History}/>
    <Route exact path="/signup" component={Membership}/>
    <Route exact path="/nba" component={NBA}/>
    <Route exact path="/fifa" component={FIFA}/>
    <Route exact path="/nfl" component={Madden}/>
    <Route exact path="/mlb" component={MLB}/>
    <Route exact path="/pes" component={PES}/>
    <Route exact path="/wwe" component={WWE2K}/>
    <Route exact path="/nhl" component={NHL}/>
    <Route exact path="/events" component={Events}/>
    <Route exact path="/success" component={Success}/>

    </Switch>
    <Footer/>
    </div>
    </Router>
  );
}

export default App;
